import {View, Text} from 'react-native';
import React, {useEffect, useRef, useState} from 'react';
// const Module1 = React.lazy(() => import('./Module1'));
const Modules = () => {
  const [Component, setComponent] = useState()
  useEffect(() => {
    (async () => {
      const c = (await import('./Module1')).default;
      setComponent(c);
    })();
  }, []);
  return (
    <View>
      <Text>Modules</Text>
      {Component ? Component : <Text>loading</Text>}
      {/* <React.Suspense>
        <Module1 />
      </React.Suspense> */}
    </View>
  );
};

export default Modules;
